


import Link from "next/link";
import { RouteNames } from "@/lib/routesInfo";
import { LatestNewsProps } from "../LatestNews/LatestNewsElement.interface";
import DefaultLatestNewsImage from '@/public/images/Default-Latest-News-Image.png'
import Image from "next/image"
import './style.css';

export default function LatestNewsElement(props: LatestNewsProps) {

    const ImageComponent = () => {
        if (props.image) {
            return (
                <Image src={props.image} alt={props.title} />
            )
        } else {
            return (
                <Image
                    alt="Зенит-Ижевск стандартная обложка свежей нвости"
                    src={DefaultLatestNewsImage}
                    className="w-full h-full object-cover"
                />
            )
        }
    };

    return (
        <div className="w-[360px] h-[480px] rounded-[30px] bg-transparent overflow-hidden relative"
            style={{
                WebkitMaskImage: '-webkit-radial-gradient(white, black)'
            }}
            >
            <ImageComponent />
            <div className="absolute transition-all opacity-100 duration: 300 inset-0 h-full z-20 hover:opacity-0 latestNewsElementGradientPassive"/>
            <div className="absolute transition-all opacity-0 duration: 300 inset-0 h-full z-20 hover:opacity-100 latestNewsElementGradientActive"/>
        </div>
    )
}